* {
  margin: 0; /* usunięcie marginesów */
  padding: 0; /* usunięcie wypełnień */
  box-sizing: border-box; /* uwzględnianie paddingu i borderów w szerokości i wysokości elementów */
}

body {
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif; /* czcionka */
  background-color: #f1f5f8; /* kolor tła body (za książkami) */
  color: #222; /* kolor tekstu (całego?) */
}

.booklist {
  width: 90vw; /* szerokość widoku maxymalnie 90% (vw) - responsywność*/
  max-width: 1170px; /* maksymalna szerokość - na dużych ekranach nie będzie problemów*/
  margin: 1rem auto; /* górny i dolny margines po 1 rem; wartości auto na bokach powodują wyśrodkowanie poziome*/
  display: grid; /* zmienia .booklist w kontener siatki (CSS Grid). Chlidren .booklist stają się elementami siatki.*/
  gap: 2rem; /* ustawia odstęp 2 rem między wierszami i kolumnami w siatce (działa jak row-gap i column-gap jednocześnie) */
}

@media screen and (min-width: 576px) /* reguła zaczyna działać dopiero,
  gdy szerokość viewportu osiągnie co najmniej 576 px */ {
  .booklist {
    grid-template-columns: repeat(
      2,
      1fr
    ); /* siatka ma 2 równe kolumny; każda kolumna zajmuje 1 frakcję dostępnej szerokości (czyli po równo 1/2 szerokości kontenera) */
    align-items: start; /* wyrównanie elementów w siatce do góry */
  }
}

@media screen and (min-width: 768px) /* reguła zaczyna działać dopiero,
  gdy szerokość viewportu osiągnie co najmniej 768 px */ {
  .booklist {
    grid-template-columns: repeat(
      3,
      1fr
    ); /* siatka ma 3 równe kolumny; każda kolumna zajmuje 1 frakcję dostępnej szerokości (czyli po równo 1/3 szerokości kontenera) */
    align-items: start; /* wyrównanie elementów w siatce do góry */
  }
}

@media screen and (min-width: 1080px) /* reguła zaczyna działać dopiero,
  gdy szerokość viewportu osiągnie co najmniej 1080 px */ {
  .booklist {
    grid-template-columns: repeat(
      4,
      1fr
    ); /* siatka ma 4 równe kolumny; każda kolumna zajmuje 1 frakcję dostępnej szerokości (czyli po równo 1/4 szerokości kontenera) */
    align-items: start; /* wyrównanie elementów w siatce do góry */
  }
}

.book {
  background: #fff; /* tło białe */
  border-radius: 1rem; /* zaokrąglone rogi */
  padding: 1rem; /* wypełnienie */
  text-align: center; /* wyśrodkowanie tekstu */
  position: relative; /* potrzebne do pozycjonowania numeru książki */
}

.book img {
  border-radius: 1rem; /* zaokrąglone rogi obrazka */
  width: 100%; /* szerokość obrazka */
  object-fit: cover; /* dopasowanie obrazu do bloku */
}

.title-row {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-top: 1rem;
  justify-content: center;
}

.book div {
  margin-top: 1rem; /* odstęp od góry */
}

.book-title {
  margin: 0; /* keep title aligned in the row */
  font-size: 1rem; /* rozmiar czcionki */
  color: black; /* kolor tekstu */
  font-style: italic; /* kursywa */
}

.book-author {
  color: blue;
  font-size: 0.85rem;
  margin-top: 0.75rem;
  margin-bottom: 0.75rem;
  font-style: italic; /* kursywa */
  text-align: center;
}

.book-number {
  font-weight: 700;
  color: black;
  min-width: 1rem;
  text-align: center;
  font-size: 1rem;
  position: absolute;
  top: 0;
  left: 0;
  padding: 0.25rem;
  border-top-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
  background-color: red;
  color: white;
}

.book-category {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  font-size: 1rem;
  color: grey;
  text-transform: uppercase;
  font-style: italic;
  letter-spacing: 1px;
}

.titleofsection {
  text-align: center;
  text-transform: uppercase;
  color: #102a42;
}

.form {
  background-color: #f1f5f8;
  padding: 2rem;
  margin: 2rem auto;
}
